import { useContext } from 'react'
import { observer } from 'mobx-react-lite'
import * as React from 'react'
import { Box, Grid, Typography } from '@mui/material'
import { RootStoreContext } from '../store/rootStoreContext'

const ScoreReport = (props) => {
  const [root] = useContext(RootStoreContext)
  const reportStyle = {
    mb: 2,
    p: 2,
  }

  const TableElem = (props) => {
    const tableElemStyle = {
      lineHeight: 1,
      fontSize: '16pt',
      fontWeight: 400,
    }

    return (
      <Typography sx={tableElemStyle}>
        <Box component='span' sx={{ textDecoration: 'underline' }}>
          {props.cat}
        </Box>
        <br />
        {props.val}
      </Typography>
    )
  }
  return (
    <Box sx={{ ...reportStyle, ...props.sx }}>
      <Grid container justifyContent='center' spacing={3}>
        <Grid item>
          <TableElem cat='Par' val={root.puzzleStore.current?.par} />
        </Grid>
        <Grid item>
          <TableElem cat='Moves' val={root.gameStore.movesMade} />
        </Grid>
        <Grid item>
          <TableElem cat='Score' val='+2' />
        </Grid>
      </Grid>
      <Box
        component='img'
        src={require('../assets/images/report.png')}
        alt=''
        sx={{
          my: 0,
          mt: 3,
          width: '80%',
          maxWidth: '200px',
        }}
      />
    </Box>
  )
}

export default observer(ScoreReport)
