import { useEffect, useContext } from 'react'
import { observer } from 'mobx-react-lite'
import { useTheme } from '@mui/material/styles'
import { Box, Button, Typography, List, ListItem } from '@mui/material'
import { RootStoreContext } from '../store/rootStoreContext'
import QuoteArea from './quoteArea'

const Splash = (props) => {
  const [root] = useContext(RootStoreContext)
  const theme = useTheme()

  useEffect(() => {
    root.puzzleStore.loadPuzzleById('splashPuzzle')
  }, [root.puzzleStore])

  const moveMade = root.gameStore.movesMade > 0
  const puzzleSolved = root.gameStore.isPuzzleSolved
  const red = theme.palette.error.main
  const green = theme.palette.correct

  return (
    <Box
      sx={{
        display: 'flex',
        alignItems: 'center',
        width: '100%',
        p: 1,
      }}
    >
      <Box
        sx={{
          background: '#ddd',
          width: '100%',
          p: 2,
          //  maxWidth: '350px',
          border: '1px solid black',
          textAlign: 'center',
        }}
      >
        <Typography
          sx={{
            fontFamily: 'Courier New, Courier',
            fontSize: ['20pt', '25pt', '30pt'],
            fontWeight: 700,
            lineHeight: 1,
            mb: 2,
          }}
        >
          Quote Scramble
        </Typography>

        <QuoteArea />

        <List sx={{ pl: [4, 6, 8], fontSize: '15pt', lineHeight: 1.2 }}>
          <ListItem
            sx={{
              display: 'list-item',
              listStyleType: 'space-counter',
              '&::marker': {
                content: moveMade ? `"✓"` : `"x"`,
                color: moveMade ? green : red,
                fontSize: '18pt',
              },
            }}
          >
            Tap any two{' '}
            <Box component='span' className='incorrect'>
              gray
            </Box>{' '}
            <Box component='span' className='incorrect'>
              words
            </Box>{' '}
            to swap them.
          </ListItem>
          <ListItem
            sx={{
              display: 'list-item',
              listStyleType: 'space-counter',
              '&::marker': {
                content: puzzleSolved ? `"✓"` : `"x"`,
                color: puzzleSolved ? green : red,
                fontSize: '18pt',
              },
            }}
          >
            Get every word back where it belongs.
          </ListItem>
        </List>

        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            mt: 4,
            justifyContent: 'space-evenly',
          }}
        >
          <Button
            variant='outlined'
            disabled={!puzzleSolved}
            sx={{}}
            onClick={() => root.gameStore.toggleShowSplash()}
          >
            practice
          </Button>
          <Button
            disabled={!puzzleSolved}
            variant='outlined'
            sx={{}}
            onClick={() => root.gameStore.toggleShowSplash()}
          >
            daily puzzle
          </Button>
        </Box>
      </Box>
    </Box>
  )
}

export default observer(Splash)
