import { useContext, useEffect, useRef } from 'react'
import { observer } from 'mobx-react-lite'
import { Box } from '@mui/material'
import { RootStoreContext } from '../store/rootStoreContext'
import QuoteArea from './quoteArea'
import PuzzleCategory from './puzzleCategory'
import HealthBar from './healthBar'
import Slider from './slider'
import Attribution from './attribution'
import SuccessDisplay from './successDisplay'

const GameBoard = (props) => {
  const hudTopRef = useRef()
  const [root] = useContext(RootStoreContext)

  useEffect(() => {
    root.puzzleStore.loadRandomPuzzle()
  }, [root.puzzleStore])

  const getSliderYTranslate = () => {
    if (hudTopRef.current && root.gameStore.puzzleState === 'playing') {
      const hudTopHeight = hudTopRef.current.offsetHeight
      const sentenceHeight = document.getElementById('sentence').offsetHeight
      const halfParentHeight =
        document.getElementById('top-layout').offsetHeight * 0.5 - 64
      return halfParentHeight - 0.5 * sentenceHeight - hudTopHeight
    } else {
      return root.gameStore.adSpaceHeight
    }
  }

  return (
    <Slider
      translateAmount={getSliderYTranslate()}
      isTranslated={root.gameStore.puzzleState !== 'succeeded'}
    >
      <Box sx={{ width: '95%', margin: '0 auto' }}>
        <Box
          id='hudTop'
          ref={hudTopRef}
          sx={{
            /* todo: move these styles to code */
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            flexWrap: 'nowrap',
            height: '26.5px',
          }}
        >
          <PuzzleCategory sx={{ pl: '4px' }} />
          <HealthBar sx={{ pr: '4px' }} />
        </Box>
        <QuoteArea id='quote' />
        <Box
          sx={{
            width: '100%',
          }}
        >
          <Attribution
            show={root.gameStore.puzzleState === 'succeeded'}
            html={root.gameStore.attributionHtml}
          />
        </Box>

        {root.gameStore.puzzleState === 'succeeded' && <SuccessDisplay />}
      </Box>
    </Slider>
  )
}

export default observer(GameBoard)
