import React, { useState, useEffect, useContext } from 'react'
import ReactGA from 'react-ga4'
import { Box, Container } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import { observer } from 'mobx-react-lite'
import { RootStoreContext } from './store/rootStoreContext'
import HelpDialog from './components/helpDialog'
import SettingsDialog from './components/settingsDialog'
import GameBoard from './components/gameBoard'
import Splash from './components/splash'
import BannerAd from './components/bannerAd'
import Menu from './components/menu'

// eslint-disable-next-line no-unused-vars
import styles from './components/gameplay/gameplay.css'

const App = () => {
  const [root] = useContext(RootStoreContext)
  const [settingsOpen, setSettingsOpen] = useState(false) //todo: move to store
  const [helpOpen, setHelpOpen] = useState(false) //todo: move to store
  const theme = useTheme()

  useEffect(() => {
    ReactGA.initialize('G-7S0H9Q0468')
  }, [])

  return (
    <Container maxWidth='sm'>
      <Box
        id='top-layout'
        sx={{
          color: theme.palette.primary.main,
          position: 'relative',
          height: '100vh',
          mt: '62px',
        }}
      >
        <BannerAd />
        {root.gameStore.showSplash ? '' : <Menu />}
        <Box
          id='aqua'
          sx={{
            display: 'flex',
            height: `100vh`,
          }}
        >
          {root.gameStore.showSplash ? (
            <Splash />
          ) : (
            <GameBoard id='gameboard' />
          )}
        </Box>

        <SettingsDialog
          open={settingsOpen}
          closeHandler={() => setSettingsOpen(false)}
        />

        <HelpDialog open={helpOpen} closeHandler={() => setHelpOpen(false)} />
      </Box>
    </Container>
  )
}

export default observer(App)
